<template>
  <div class="generate">
    <div class="container">
      <div class="columns is-multiline">
        <div class="column is-12">
          <Aads1></Aads1>
        </div>
        <div class="column is-3">
          <Juicy1></Juicy1>
        </div>
        <div class="column is-6">
          <div class="blockito">

            <div class="box">
              <h1 class="is-size-4 has-text-primary">{{ generate.name }}</h1>
              <hr>
              <Aads2></Aads2>
              <center>
                <div v-if="isCount" class="tag is-primary">
                  {{ countDown }}
                </div>
                <template v-else>
                  <Aads2></Aads2>
                  <b-button
                    type="is-success"
                    @click.prevent="getLinkGenerate"
                    :label="labelButtonGetLink"
                    :disabled="activeButtonGetLink"
                    :loading="loadingButtonGetLink"
                    v-if="!activeUrl"></b-button>
                </template>


                <a class="button is-primary" :href="url" v-if="activeUrl">GO!</a>
              </center>
            </div>
          </div>
        </div>
        <div class="column is-3">
          <Juicy2></Juicy2>
        </div>
        <div class="column is-12">
          <Aads1></Aads1>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Link from '../../api/Link'

import Aads2 from '@/components/ads/A-ADS2.vue'
import Aads1 from '@/components/ads/A-ADS1.vue'
import Juicy1 from '@/components/ads/Juicy1.vue'
import Juicy2 from '@/components/ads/Juicy2.vue'


export default {
  name: 'WebGenerate',
  components: {
    Aads1, Aads2, Juicy1, Juicy2
  },
  data () {
    return {
      generate: {},
      countDown: 10,
      isCount: true,
      form: {
        hash: null
      },
      link_generate_id: null,
      labelButtonGetLink: 'Clic Here!',
      activeButtonGetLink: false,
      loadingButtonGetLink: false,
      url: null,
      activeUrl: false
    }
  },
  methods: {
    myCountDown: function () {
      if(this.countDown > 0){
        setTimeout(() => {
          this.countDown -= 1
          this.myCountDown()
        }, 1000)
      }else{
        this.isCount = false
      }
    },
    getLinkGenerate: function () {
      Link.getLinkGenerate(this.form, this.link_generate_id).then((response) => {
        this.loadingButtonGetLink = true
        if(response.status === 200) {
          this.loadingButtonGetLink = false
          if(response.data.url){
            this.url = response.data.url
            this.activeUrl = true
          }else if(response.data.expired){
            this.loadingButtonGetLink = false
            this.$buefy.notification.open({
              message: 'Url Expired, tryng again',
              type: 'is-info'
            })
            this.labelButtonGetLink = "Expired!"
            this.activeButtonGetLink = true
          }
        }
      }).catch((errors) => {
        if(errors.response.status === 404){
          this.$router.push({name: '404'})
        }

      })
    }
  },
  mounted () {
    Link.getGenerate(this.$route.params.id, this.$route.params.hash).then((response) => {
      if(response.status === 200) {
        this.generate = response.data
        this.link_generate_id = response.data.id
        this.form.hash = response.data.hash
      }
    }).catch((errors) => {
      if(errors.response.status === 404){
        this.$router.push({name: '404'})
      }
    }),

    this.myCountDown()
  }
}
</script>
